/**
 * ë¬´í•œ ë£¨í”„ ë°©ì§€ í”Œë˜ê·¸
 */
let isProcessing = false;

/**
 * ëŒ€ê¸° ì¤‘ì¸ confirm í”„ë¡œë¯¸ìŠ¤ë“¤ì„ ì €ì¥
 */
const pendingConfirms: Map<string, { resolve: (value: boolean) => void, reject: () => void }> = new Map();

